import React, { useEffect, useRef, useState, useContext } from 'react';
import styled from 'styled-components'
import NavBar from '../components/NavBar'
import { mainStore, MainStateProvider } from '../store/MainStore'
import styles from '../constants'
import Padding from './Padding'




import MenuIcon from '../components/MenuIcon';
import TransitionContainer from '../components/TransitionContainer';
import ArtistsContainer  from '../components/ArtistsContainer'
import ArtistsInfoContainer  from '../components/ArtistsInfoContainer'

import { MdEmail, MdCancel, MdCheck, MdKeyboardArrowDown, MdKeyboardArrowUp, MdSearch, MdColorLens, MdFitnessCenter, MdLocalActivity, MdLocalBar, MdLocalCafe, MdLocalMall, MdRestaurantMenu, MdShoppingCart } from 'react-icons/md';
import Content from './Content';
import MenuPage from './MenuPage';

const Container = styled.div`
    top: 0px;
    left: 0px;
    position: absolute;
    height: 100%;
    width: 100%;
    overflow: hidden;
`

const ContentContainer = styled.div`
    top: 0px;
    left: 0px;
    position: absolute;
    height: 100%;
    width: 100%;
    overflow: hidden;
`


//    scroll-snap-type: y mandatory;

const height  = window.innerHeight
const width  = window.innerWidth

interface propTypes {

}

interface ContentHeightTypes {
    name: string
    height: number
}

function MainPage(props: propTypes) {
    const {state, dispatch} = useContext(mainStore)
    const [mouseX, setMouseX] = useState(0)
    const [mouseY, setMouseY] = useState(0)
    const [contentScroll, setContentScroll] = useState(0)
    const [oldY, setOldY] = useState(0)

    const [contentHeights, setContentHeights] = useState<ContentHeightTypes[]>()

    let padding = 90

    if(state.mobileOpen) 
        padding = 60


    let timer:any = null


    useEffect(() => {


        return () => {
            clearTimeout(timer)
        }
    
    }, [])

    return (
        <Container
            id="container"
            onMouseMove={calculateMousePerc}
        >
            

             

            
            
            <Padding 
                menuSetter={menuSetter}
                setArtist={setArtist}
                >
               <ContentContainer>
                    <Content 
                        mouseX={mouseX}
                        mouseY={mouseY}
                        setArtist={setArtist}
                        onContentScroll={onContentScroll}
                    />
                    <ArtistsContainer 
                        setArtist={setArtist}
                    />

                    <ArtistsInfoContainer 
                    
                    />

                    <MenuPage 
                    /> 

                </ContentContainer>
                
            </Padding>

        </Container>
    );

    function saveHeight(height: number, name: string) {
        let newBlank:any = []
        let newHeights = newBlank.concat(contentHeights)
        let index = -1
        newHeights.forEach((item: any, key:number) => {
            if(item.name = name) {
                index = key
            }
        })
        if(index > -1) {
            newHeights[index].height = height
        }
        else {
            let newHeight = {name, height}
            newHeights.push(newHeight)
            setContentHeights(newHeights)
        }
        
    }

    function onContentScroll(scroll: number) {
        setContentScroll(scroll)

        recalculate()
    }

    function recalculate() {
        let newY = ((oldY + contentScroll)/height) * 100


        setMouseY( newY)
    }

    function calculateMousePerc(e:any) {
        let newX = ((e.clientX)/width) * 100

        let newY = ((e.clientY + contentScroll)/height) * 100
        setOldY(e.clientY)

        if(Math.abs(mouseX - newX) > 1 )    {
            setMouseX( newX)
        }
        if(Math.abs(mouseY - newY) > 1 )    {
            setMouseY( newY)
        }
    }
    

    

    function goTo(page: string) {
        /*let newBlank:any = []
        let heights = newBlank.concat(contentHeights)
        let height = =
        switch(page) {
            case "":
                height = 1
                break
            case "meet":
                height = 2
                break
            case "gallery":
                height = 3
                break
            case "events":
                height = 4
                break
            case "contact":
                height = 5
                break
        }

        setScroll(multiplier * height)
        menuSetter(false)
        */
    }

    function setArtist(artist: string, artistType: string) {
        dispatch({
            type: 'setArtist',
            artistOpen: artist,
            artistType: artistType
        })
    }

    function menuSetter(val: boolean) {
        setMenuOpen(val)
    }

    

    function setScroll(top: number) {
        let scroller = document.getElementById("container")
        if(scroller) 
            scroller.scrollTop = top
    }

    function setMenuOpen(val: boolean) {
        dispatch({
            type: 'setMenu',
            menuOpen: val
        })
    }
}

export default MainPage;