import React, { useEffect, useContext, useState } from 'react';
import styled from 'styled-components'
import { mainStore, MainStateProvider } from '../store/MainStore'
import styles from '../constants'
import SlideContainer from './SlideContainer';
import JewelryArtists from './JewelryArtists';
import VisualArtists from './VisualArtists';
import ArtistInfo from './ArtistInfo';

const Container = styled.div`
    position: absolute;
    z-index: 13;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease;
    will-change: transform;

`





interface propTypes {
}


function ArtistContainer(props: propTypes) {
    const {state, dispatch} = useContext(mainStore)
    const [open, setOpen] = useState(true)
    const [preLoading, setPreLoading] = useState(true)


    const [artistTypeIndex, setArtistTypeIndex] = useState(0)


    useEffect(() => {
        if(!preLoading) {
            if(state.artistOpen !== '') {
                console.log('hello'+state.artistOpen)

                for(let i = 0 ; i < state.allArtists.length; i++) {
                    if(state.allArtists[i].name === state.artistOpen) {
                        setArtistTypeIndex(i)
                        setOpen(true)
                        break
                    }
                }

                console.log(artistTypeIndex)
            }
            if(state.artistOpen === '')
                setOpen(false)
            else
                setOpen(true)

        }

    }, [state.artistOpen])

    useEffect(() => {
        let timer1:any = null

        console.log('hello')

            
        timer1 = setTimeout(() => {
            setOpen(false);
            setPreLoading(false) 
        }, 3000)
        

        return () => {
            clearTimeout(timer1)
        }
    },[])

    return (
        <Container
            style={{
                transform: open ? 'translateY(0%)' : 'translateY(100%)',
            }}
        >
                
                <SlideContainer
                    slides={
                        state.allArtists.map((prop:any) => { 
                            return <ArtistInfo artist={prop} />
                        })
                    }
                    open={open}
                    index={artistTypeIndex}
                />
            
            
        </Container>
    );
}

export default ArtistContainer;